import { Theme, createStyles, WithStyles, withStyles, List, Box, ExpansionPanel, ExpansionPanelSummary, Typography, ExpansionPanelDetails, ListItem, ListItemText, Tooltip } from "@material-ui/core";

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Issue, WcagSection, IssueType } from "./models";
import React from "react";

interface InternalStylesProps extends WithStyles<typeof styles>{}

export interface IssuesListProps extends InternalStylesProps {
    issues: Issue[];
    issueType: IssueType;
};

const styles = (theme: Theme) => createStyles({
    messageListItem: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start'
    },
    errorPanelHeader: {
        backgroundColor: theme.palette.error.main,
        color: theme.palette.error.contrastText
    },
    errorExpandIcon: {
        color: theme.palette.error.contrastText
    },
    warningPanelHeader: {
        backgroundColor: theme.palette.warning.main,
        color: theme.palette.warning.contrastText
    },
    warningExpandIcon: {
        color: theme.palette.warning.contrastText
    },
    noticePanelHeader: {
        backgroundColor: theme.palette.info.main,
        color: theme.palette.info.contrastText
    },
    noticeExpandIcon: {
        color: theme.palette.info.contrastText
    },
});

interface GroupedIssue {
    section: WcagSection;
    hasSubgroup?: boolean;
    issues: Issue[] | GroupedIssue[];
    count: number;
}

interface IssueMessageItem {
    selector: string;
    element: string;
}

interface IssueMessage {
    criterion: string;
    message: string;
    count: number;
    techniques: string[];
    items: IssueMessageItem[];
};

const groupByMessage = (issues: Issue[]): IssueMessage[] => {
    let groupedObj = issues.reduce((prev, cur) => {
        let { message, selector, element, code } = cur;
        let { criterion, techniques } = code;
        let { handle, num } = criterion;

        let issueMessage: IssueMessage = prev[message] || { criterion: `${num}. ${handle}`, message: message, techniques: techniques, items: [], count: 0 };
        issueMessage.items.push({ selector: selector, element: element });
        issueMessage.count = issueMessage.items.length;
        prev[message] = issueMessage;
        return prev;
    }, {});
    return Object.values(groupedObj);
};

const groupBySection = (issues: Issue[], keyFn: (issue: Issue) => WcagSection): GroupedIssue[] => {
    let groupedObj = issues.reduce((prev, cur) => {
        let keyObj = keyFn(cur);
        let keyStr = keyObj.num;

        let groupedIssue: GroupedIssue = prev[keyStr] || { section: keyObj, issues: [], count: 0 };
        groupedIssue.issues.push(cur as Issue & GroupedIssue);
        groupedIssue.count = groupedIssue.issues.length;

        prev[keyStr] = groupedIssue;
        return prev;
    }, {});

    return Object.values(groupedObj);
};

const messageList = (issueMessages: IssueMessage[], stylesProps: InternalStylesProps) => {
    const {classes} = stylesProps;

    return (
        <ul>
            {issueMessages.map((im, imIndex) =>
                <li key={imIndex}>
                    <Typography component="span">
                        {im.criterion}: {im.message}
                    </Typography>
                    <Typography component="div">
                        Techniques: {im.techniques.join(',')}
                    </Typography>
                    <List>
                        {im.items.map((item, itemIndex) =>
                            <ListItem key={itemIndex} className={classes.messageListItem} >
                                <Typography color="primary" component="div">
                                    <em>{item.selector}</em>
                                </Typography>
                                <Typography gutterBottom component="div">
                                    <code>{item.element}</code>
                                </Typography>
                            </ListItem>
                        )}
                    </List>
                </li>
            )}
        </ul>
    );
};

const sectionPanel = (groupedIssue: GroupedIssue, issueType: IssueType, stylesProps: InternalStylesProps) => {
    const { hasSubgroup, section, issues, count } = groupedIssue;
    const { num, handle, title } = section;
    const {classes} = stylesProps;

    let panelHeaderClassName: string;
    let expandIconClassName: string;
    if (issueType == IssueType.Error) {
        panelHeaderClassName = classes.errorPanelHeader;
        expandIconClassName = classes.errorExpandIcon;
    } else if (issueType == IssueType.Warning) {
        panelHeaderClassName = classes.warningPanelHeader;
        expandIconClassName = classes.warningExpandIcon;
    } else if (issueType == IssueType.Notice) {
        panelHeaderClassName = classes.noticePanelHeader;
        expandIconClassName = classes.noticeExpandIcon;
    }

    return (
        <ExpansionPanel style={{ width: '100%' }}>
            <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon className={expandIconClassName} />}
                aria-controls={`panel-${num}-content`}
                id={`panel-${num}-header`}
                className={panelHeaderClassName}
            >
            <Typography component="span">{num}. {handle} <strong>({count})</strong></Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails style={{ flexDirection: 'column' }}>
            <Typography guttersBottom>Info: <em>{title}</em></Typography>
                {hasSubgroup ?
                    <List>
                        {(issues as GroupedIssue[]).map(gi =>
                            <ListItem disableGutters key={gi.section.num} component="li">
                                {sectionPanel(gi, issueType, stylesProps)}
                            </ListItem>
                        )}
                    </List>
                    : messageList(groupByMessage(issues as Issue[]), stylesProps)}
            </ExpansionPanelDetails>
        </ExpansionPanel>
    );
};

const IssuesList = (props: IssuesListProps) => {
    const { issues, issueType, ...stylesProps } = props;

    const groupedIssues = groupBySection(issues, (issue) => issue.code.principle)
        .map((gp): GroupedIssue => {
            return {
                section: gp.section,
                hasSubgroup: true,
                issues: groupBySection(gp.issues as Issue[], (issue) => issue.code.guideline),
                count: gp.issues.length
            };
        });

    return (
        <List>
            {groupedIssues.map((gi) =>
                <ListItem disableGutters key={gi.section.num} component="li">
                    {sectionPanel(gi, issueType, stylesProps)}
                </ListItem>
            )}
        </List>
    );
};

export default withStyles(styles)(IssuesList);